
.opening-god {
  position: absolute;
  top: 31%;
  right: 14%;
  height: 300px;
}

.flex-dialog {
  display: flex;
  flex-basis: auto;
  flex-direction: column;
  margin-left: 30%;
}

.hide {
  display: none;
}

p {
  color:white;
}


.text {
  color: white;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
